@import "./node_modules/magnific-popup/dist/magnific-popup"

.my-mfp-zoom-in
  .mfp-animate
    opacity: 0
    -webkit-transition: all 0.2s ease-in-out
    -moz-transition: all 0.2s ease-in-out
    -o-transition: all 0.2s ease-in-out
    transition: all 0.2s ease-in-out
    -webkit-transform: scale(0.8)
    -moz-transform: scale(0.8)
    -ms-transform: scale(0.8)
    -o-transform: scale(0.8)
    transform: scale(0.8)
  &.mfp-ready .mfp-animate
    opacity: 1
    -webkit-transform: scale(1)
    -moz-transform: scale(1)
    -ms-transform: scale(1)
    -o-transform: scale(1)
    transform: scale(1)

  &.mfp-removing .mfp-animate
    -webkit-transform: scale(0.8)
    -moz-transform: scale(0.8)
    -ms-transform: scale(0.8)
    -o-transform: scale(0.8)
    transform: scale(0.8)
    opacity: 0

  &.mfp-bg
    opacity: 0
    -webkit-transition: opacity 0.3s ease-out
    -moz-transition: opacity 0.3s ease-out
    -o-transition: opacity 0.3s ease-out
    transition: opacity 0.3s ease-out

  &.mfp-ready.mfp-bg
    opacity: 0.8
  &.mfp-removing.mfp-bg
    opacity: 0
/* modals */
.backcall-modal
  background-color: #fff
  border-radius: 30px
  max-width: 345px
  margin: 0 auto
  padding: 40px 20px
  position: relative
  @media (max-width: 360px)
    padding: 40px 15px
  @media (min-width: 450px)
    max-width: 447px
  .modal-close
    position: absolute
    color: rgba(#000, .35)
    font-size: 22px
    line-height: 22px
    top: 10px
    right: 10px
    transition: color .3s
    &:hover
      color: #000
  .modal-title
    text-align: center
    font-weight: 700
    font-size: 20px
    line-height: 24px
  .modal-form
    margin-top: 30px
    .input-group
      margin-top: 25px
      &:nth-of-type(1)
        margin-top: 0
      .input-style
        border: 1px solid #C4C4C4
      textarea
        min-height: 120px
        max-width: 100%
      &.check-group
        margin-top: 12px
        label
          color: #747F87
          span
            padding-left: 37px
            &:before
              border-color: #747F87
          input
            &:checked + span:before
              color: #747F87
          a
            color: #747F87
.lesson-modal
  background-color: #fff
  border-radius: 30px
  max-width: 345px
  margin: 0 auto
  padding: 40px 20px 20px
  position: relative
  @media (min-width: 400px)
    max-width: 375px
  @media (min-width: 576px)
    max-width: 475px
  @media (min-width: 960px)
    max-width: 917px
    padding: 0
  .modal-close
    position: absolute
    color: rgba(#000, .35)
    font-size: 22px
    line-height: 22px
    top: 10px
    right: 10px
    transition: color .3s
    &:hover
      color: #000
  .modal-content
    padding: 15px 0
    @media (min-width: 960px)
      display: flex
      align-items: flex-start
      justify-content: flex-start
      flex-wrap: wrap
      position: relative
      padding: 50px
  .modal-title
    text-align: center
    font-weight: 700
    font-size: 20px
    line-height: 23px
    color: $accent-color
    text-align: center
    @media (min-width: 960px)
      flex: 0 0 100%
      max-width: 100%
      padding-right: 55%
      font-size: 26px
      line-height: 39px
      text-align: left
      margin-bottom: -100%
  .modal-form
    background-color: $gray-color
    width: calc(100% + 40px)
    margin: 30px -20px
    border-radius: 40px
    padding: 30px 22px
    @media (max-width: 360px)
      padding: 30px 10px
    @media (min-width: 576px)
      width: 100%
      margin: 30px auto
    @media (min-width: 960px)
      flex: 0 0 50%
      max-width: 50%
      margin: 0
      order: 2
    .form-title
      text-align: center
      font-weight: 700
      font-size: 18px
      line-height: 21px
    form
      margin-top: 20px
      .input-group
        margin-top: 25px
        &:nth-of-type(1)
          margin-top: 0
        .input-style
          border: 1px solid #C4C4C4
        textarea
          min-height: 120px
          max-width: 100%
        &.check-group
          margin-top: 15px
          label
            color: #747F87
            span
              padding-left: 45px
              &:before
                border-color: #747F87
            input
              &:checked + span:before
                color: #747F87
            a
              color: #747F87
  .modal-descr
    margin-top: 20px
    @media (min-width: 960px)
      flex: 0 0 45%
      max-width: 45%
      margin: 150px auto 0 0
    ul
      margin: 0
      padding: 0
      list-style-type: none
      line-height: 21px
      li
        display: block
        width: 100%
        position: relative
        padding-left: 35px
        margin-top: 20px
        letter-spacing: .35px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e805'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          position: absolute
          top: 0
          left: 2px
          font-size: 18px
          line-height: 18px
    .gift
      margin-top: 20px
      display: flex
      align-items: center
      justify-items: flex-start
      .gift-icon
        display: block
        width: auto
        max-width: 70px
        flex: 0 0 70px
        position: relative
        right: 3px
      .gift-descr
        flex: 0 0 calc(100% - 70px)
        max-width: calc(100% - 70px)
        padding-left: 12px
        line-height: 21px
        letter-spacing: .35px

.person-modal
  background-color: #fff
  border-radius: 30px
  padding: 45px 24px 25px
  position: relative
  @media (max-width: 340px)
    padding: 45px 15px 25px
  @media (min-width: 576px)
    max-width: 450px
    margin: 0 auto
  @media (min-width: 960px)
    max-width: 792px
    padding: 40px
    border-radius: 40px
  .modal-close
    position: absolute
    color: rgba(#000, .35)
    font-size: 22px
    line-height: 22px
    top: 10px
    right: 10px
    transition: color .3s
    &:hover
      color: #000
  .mperson-info
    display: flex
    align-items: flex-start
    justify-items: flex-start
    flex-wrap: wrap
    &__image
      flex: 0 0 100px
      max-width: 100px
      @media (min-width: 960px)
        flex: 0 0 180px
        max-width: 180px
      .person-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
        border-radius: 50%
        @media (min-width: 960px)
          width: 100%
    &__name
      flex: 0 0 100%
      width: 100%
      max-width: 100%
      @media (min-width: 960px)
        flex: 0 0 calc(100% - 180px)
        max-width: calc(100% - 180px)
        padding-left: 35px
      .person-name
        display: flex
        align-items: center
        justify-content: flex-start
        height: 100px
        position: absolute
        padding-left: 120px
        top: 45px
        left: 25px
        font-weight: 700
        font-size: 18px
        line-height: 27px
        letter-spacing: .3px
        @media (min-width: 960px)
          display: block
          margin-top: 10px
          position: relative
          top: 0
          left: 0
          padding-left: 0
          height: auto
      .audio-block
        width: 100%
        min-width: 100%
        box-shadow: none
        background-color: transparent
        height: auto
        padding: 0
        margin-top: 10px
        border-radius: 0
        @media (min-width: 960px)
          margin-top: 15px
          width: 380px
        .holder
          .play-pause-btn
            display: flex
            align-items: center
            justify-items: center
            width: 42px
            height: 42px
            border-radius: 50%
            background-color: $accent-color
            transition: background-color .3s
            position: relative
            &:after
              content: '\e80b'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 50%
              transform: translate(-50%, -50%)
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              color: #fff
              position: absolute
              top: 0
              left: 1px
              font-size: 18px
              line-height: 20px
            svg
              display: none
              max-width: 12px
            &[aria-label="Pause"]
              &:after
                display: none
              svg
                display: block
                margin: 0 auto
                path
                  fill: #fff
            &:hover
              background-color: $accent-darkness-color
        .controls
          position: relative
          &__current-time
            position: absolute
            right: 0
            bottom: 4px
            font-family: Roboto,sans-serif
            font-size: 10px
            line-height: 12px
          &__total-time
            display: none
          &__slider
            margin-left: 5px
            height: 2px
            background-color: #747474
            margin-right: 0
            .gap-progress
              background-color: $accent-color
              .pin
                width: 14px
                height: 14px
            .controls__progress .progress__pin
              background-color: $accent-color
        .volume
          display: none
      .try-block
        margin-top: 18px
        padding-top: 20px
        display: flex
        align-items: center
        justify-items: flex-start
        flex-wrap: wrap
        border-top: 2px solid #E3E7EB
        @media (min-width: 960px)
          margin-top: 15px
          padding-top: 15px
        span
          display: inline-block
          font-size: 14px
          line-height: 21px
          letter-spacing: .35px
          color: #000
          flex: 0 0 100%
          width: 100%
          @media (min-width: 960px)
            flex: 1 1 auto
            width: auto
        .try-btn
          display: block
          width: 100%
          white-space: nowrap
          margin-top: 10px
          font-size: 14px
          line-height: 20px
          letter-spacing: .3px
          @media (min-width: 960px)
            width: 222px
            margin-top: 0
    &__descr
      margin-top: 30px
      display: flex
      align-items: stretch
      justify-items: flex-start
      flex-wrap: wrap
      @media (min-width: 960px)
        margin-top: 60px
      &__left-side
        width: 100%
        flex: 0 0 100%
        @media (min-width: 960px)
          flex: 0 0 60%
          max-width: 60%
          padding-right: 15px
      &__right-side
        width: 100%
        flex: 0 0 100%
        margin-top: 20px
        @media (min-width: 960px)
          margin-top: 0
          flex: 0 0 calc(35% - 10px)
          max-width: calc(35% - 10px)
          margin-left: auto
      .info-item
        font-size: 14px
        line-height: 21px
        color: #747F87
        letter-spacing: .3px
        p
          margin-top: 10px
          span
            font-weight: 700
            color: #000
          &:nth-of-type(1)
            margin-top: 0
        ul
          margin: 20px 0 0
          padding: 0
          list-style-type: none
          font-weight: 700
          color: #000
          li
            display: block
            margin-top: 18px
            &:nth-of-type(1)
              margin-top: 0
        .message-block
          margin-top: 32px
          color: $txt-color
          max-height: 180px
          overflow: hidden
          text-overflow: ellipsis
          transition: height .5s
          @media (min-width: 576px)
            max-height: 160px
          &.show
            max-height: 800px
        .message-show
          display: flex
          align-items: center
          justify-content: center
          width: 100%
          .show-btn
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            color: #fff
            font-size: 8px
            line-height: 24px
            width: 24px
            height: 24px
            background-color: $accent-color
            border-radius: 50%
            text-decoration: none
            transition: background-color .3s
            .icon
              display: block
              margin: 0 auto
              width: 12px
              height: 24px
              position: relative
              text-align: center
            &.open
              .icon
                transform: scale(1,-1)
            &:hover
              background-color: $accent-darkness-color
