/* breadcrumbs */
.breadcrumbs {
  padding: 15px 0;
}
.breadcrumbs__list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.breadcrumbs__list__item {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex: 0 0 auto;
  width: auto;
  margin: 0 0 10px 0;
  position: relative;
}
.breadcrumbs__list__item:after {
  content: "\e803";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0 10px;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  font-size: 12px;
  line-height: 18px;
}
.breadcrumbs__list__item span {
  font-family: "Droid Sans", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.03em;
  color: #000;
}
.breadcrumbs__list__item:last-child:after {
  display: none;
}
.breadcrumbs__list__link {
  color: #B3B1BA;
  font-family: "Droid Sans", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.3px;
  transition: color 0.3s;
  text-decoration: underline !important;
}
.breadcrumbs__list__link:hover {
  color: #F96791;
  text-decoration: none !important;
}

/* forms */
.input-group {
  position: relative;
}
.input-group .input-style {
  display: block;
  width: 100%;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid transparent;
  font-size: 16px;
  line-height: normal;
  padding: 15px 20px;
  transition: color 0.3s, border-color 0.3s;
}
.input-group .input-style.placeholder {
  color: #747F87;
  transition: color 0.3s;
}
.input-group .input-style::-webkit-input-placeholder {
  color: #747F87;
  transition: color 0.3s;
}
.input-group .input-style::-moz-placeholder {
  color: #747F87;
  transition: color 0.3s;
}
.input-group .input-style:-moz-placeholder {
  color: #747F87;
  transition: color 0.3s;
}
.input-group .input-style:-ms-input-placeholder {
  color: #747F87;
  transition: color 0.3s;
}
@media (min-width: 1200px) {
  .input-group .input-style {
    padding: 13px 20px;
  }
}
.input-group .input-style.error {
  border-color: red !important;
}
.input-group .input-style.error.placeholder {
  color: #F96791;
}
.input-group .input-style.error::-webkit-input-placeholder {
  color: #F96791;
}
.input-group .input-style.error::-moz-placeholder {
  color: #F96791;
}
.input-group .input-style.error:-moz-placeholder {
  color: #F96791;
}
.input-group .input-style.error:-ms-input-placeholder {
  color: #F96791;
}
.input-group .input-message {
  display: none;
  width: 100%;
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  font-size: 12px;
  line-height: 16px;
  color: #000;
  letter-spacing: 0.25px;
}
.input-group .input-message .icon {
  display: inline-block;
  font-size: 16px;
  line-height: 16px;
  margin: 0 12px 0 0;
  position: relative;
  top: 1px;
  left: 1px;
}
.input-group.check-group label {
  display: inline-block;
  position: relative;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 0.25px;
  color: #fff;
  cursor: pointer;
}
.input-group.check-group label span {
  display: block;
  position: relative;
  padding-left: 40px;
}
.input-group.check-group label span:before {
  content: "\e800";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  position: absolute;
  top: 0;
  left: 3px;
  width: 20px;
  height: 20px;
  border: 2px solid #fff;
  border-radius: 2px;
  color: transparent;
  transition: color 0.3s;
  font-size: 12px;
  line-height: 18px;
}
.input-group.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
}
.input-group.check-group label input:checked + span:before {
  color: #fff;
}
.input-group.check-group label a {
  display: inline-block;
  color: #fff;
}
.input-group.check-group label a:hover {
  text-decoration: none;
}
.input-group.check-group label.black-style {
  color: #000;
}
.input-group.check-group label.black-style span:before {
  border-color: #000;
}
.input-group.check-group label.black-style input:checked + span:before {
  color: #000;
}
.input-group.check-group label.black-style a {
  color: #000;
}

.btn-style {
  display: block;
  width: 100%;
  border: none;
  text-align: center;
  color: #fff;
  border-radius: 70px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.35px;
  line-height: normal;
  padding: 13px 10px;
}
.btn-style.fill-green-style {
  background-color: #6FDE9B;
  transition: background-color 0.3s, opacity 0.3s;
}
.btn-style.fill-green-style:hover {
  background-color: #49A26D;
}
.btn-style.fill-pink-style {
  background-color: #F96791;
  transition: background-color 0.3s, opacity 0.3s;
}
.btn-style.fill-pink-style:hover {
  background-color: #EE4F62;
}
.btn-style.stroke-black-style {
  border: 2px solid #000;
  color: #000;
  font-size: 12px;
  line-height: normal;
  padding: 15px 10px;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s, opacity 0.3s;
}
.btn-style.stroke-black-style:hover {
  border-color: #F96791;
  background-color: #F96791;
  color: #fff;
}
.btn-style.stroke-white-style {
  border: 2px solid #fff;
  color: #fff;
  font-size: 12px;
  line-height: normal;
  padding: 15px 10px;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s, opacity 0.3s;
}
.btn-style.stroke-white-style:hover {
  border-color: #F96791;
  background-color: #F96791;
  color: #fff;
}
.btn-style.stroke-pink-style {
  border: 2px solid #F96791;
  color: #F96791;
  font-size: 12px;
  line-height: normal;
  padding: 15px 10px;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s, opacity 0.3s;
}
.btn-style.stroke-pink-style:hover {
  border-color: #F96791;
  background-color: #F96791;
  color: #fff;
}
.btn-style:disabled {
  opacity: 0.5;
}

