@import "../settings"
/* titles */
.section-title
  font-size: 22px
  line-height: 25px
  color: #000
  text-align: center
  font-weight: 700
  letter-spacing: .55px
  @media (min-width: 576px)
    font-size: 24px
    line-height: 28px
  @media (min-width: 768px)
    font-size: 26px
    line-height: 30px
  @media (min-width: 960px)
    font-size: 28px
    line-height: 32px
  @media (min-width: 1200px)
    font-size: 32px
    line-height: 40px
.page-title
  font-weight: 700
  font-size: 26px
  line-height: 34px
  color: #000
  letter-spacing: .55px
  @media (min-width: 576px)
    font-size: 28px
    line-height: 38px
  @media (min-width: 768px)
    font-size: 28px
    line-height: 38px
  @media (min-width: 960px)
    font-size: 30px
    line-height: 40px
  @media (min-width: 1200px)
    font-size: 37px
    line-height: 48px
/* breadcrumbs */
.breadcrumbs
  padding: 15px 0
  &__list
    display: flex
    align-items: center
    justify-content: flex-start
    flex-wrap: wrap
    &__item
      display: inline-flex
      align-items: center
      justify-content: flex-start
      flex: 0 0 auto
      width: auto
      margin: 0 0 10px 0
      position: relative
      &:after
        content: '\e803'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0 10px
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 12px
        line-height: 18px
      span
        font-family: "Droid Sans", sans-serif
        font-weight: 700
        font-size: 14px
        line-height: 18px
        letter-spacing: .03em
        color: #000
      &:last-child
        &:after
          display: none
    &__link
      color: #B3B1BA
      font-family: "Droid Sans", sans-serif
      font-weight: 700
      font-size: 14px
      line-height: 18px
      letter-spacing: .3px
      transition: color .3s
      text-decoration: underline !important
      &:hover
        color: $accent-color
        text-decoration: none !important
/* forms */
.input-group
  position: relative
  .input-style
    display: block
    width: 100%
    background-color: #fff
    border-radius: $input-border-radius
    border: $input-border
    font-size: 16px
    line-height: normal
    padding: $input-padding
    transition: color .3s, border-color .3s
    @include input-placeholder
      color: $input-placeholder-color
      transition: color .3s
    @media (min-width: 1200px)
      padding: 13px 20px
    &.error
      border-color: $input-error-border-color !important
      @include input-placeholder
        color: $accent-color
  .input-message
    display: none
    width: 100%
    position: absolute
    top: calc(100% + 2px)
    left: 0
    font-size: 12px
    line-height: 16px
    color: #000
    letter-spacing: .25px
    .icon
      display: inline-block
      font-size: 16px
      line-height: 16px
      margin: 0 12px 0 0
      position: relative
      top: 1px
      left: 1px
  &.check-group
    label
      display: inline-block
      position: relative
      font-size: 11px
      line-height: 15px
      letter-spacing: .25px
      color: #fff
      cursor: pointer
      span
        display: block
        position: relative
        padding-left: 40px
        &:before
          content: '\e800'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: flex
          align-items: center
          justify-content: center
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          position: absolute
          top: 0
          left: 3px
          width: 20px
          height: 20px
          border: 2px solid #fff
          border-radius: 2px
          color: transparent
          transition: color .3s
          font-size: 12px
          line-height: 18px
      input
        position: absolute
        top: 0
        left: 0
        width: 0
        height: 0
        visibility: hidden
        opacity: 0
        &:checked + span:before
          color: #fff
      a
        display: inline-block
        color: #fff
        &:hover
          text-decoration: none
      &.black-style
        color: #000
        span
          &:before
            border-color: #000
        input
          &:checked + span:before
            color: #000
        a
          color: #000
.btn-style
  display: block
  width: 100%
  border: none
  text-align: center
  color: #fff
  border-radius: 70px
  text-transform: uppercase
  font-weight: 700
  font-size: 16px
  letter-spacing: .35px
  line-height: normal
  padding: 13px 10px
  &.fill-green-style
    background-color: $green-color
    transition: background-color .3s, opacity .3s
    &:hover
      background-color: $green-darkness-color
  &.fill-pink-style
    background-color: $accent-color
    transition: background-color .3s, opacity .3s
    &:hover
      background-color: $accent-darkness-color
  &.stroke-black-style
    border: 2px solid #000
    color: #000
    font-size: 12px
    line-height: normal
    padding: 15px 10px
    transition: background-color .3s, border-color .3s, color .3s, opacity .3s
    &:hover
      border-color: $accent-color
      background-color: $accent-color
      color: #fff
  &.stroke-white-style
    border: 2px solid #fff
    color: #fff
    font-size: 12px
    line-height: normal
    padding: 15px 10px
    transition: background-color .3s, border-color .3s, color .3s, opacity .3s
    &:hover
      border-color: $accent-color
      background-color: $accent-color
      color: #fff
  &.stroke-pink-style
    border: 2px solid $accent-color
    color: $accent-color
    font-size: 12px
    line-height: normal
    padding: 15px 10px
    transition: background-color .3s, border-color .3s, color .3s, opacity .3s
    &:hover
      border-color: $accent-color
      background-color: $accent-color
      color: #fff
  &:disabled
    opacity: .5