@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

/* main page */
.start-form
  margin-top: 45px
  padding-top: 115px
  position: relative
  overflow: hidden
  @media (min-width: 1200px)
    margin-top: -80px
    padding-top: 175px
  &:before
    content: ''
    display: block
    width: 164px
    height: 164px
    background-image: url(../images/common/main/maria_1.png)
    background-repeat: no-repeat
    background-position: center
    background-size: contain
    position: absolute
    top: -5px
    left: 52px
    z-index: 1
    @media (max-width: 360px)
      left: 25px
    @media (min-width: 768px)
      left: calc(50% - 150px)
    @media (min-width: 1024px)
      left: calc(33.3% - 70px)
    @media (min-width: 1200px)
      left: calc(50% - 192px)
      width: 242px
      height: 242px
  &:after
    content: ''
    display: block
    width: 105px
    height: 105px
    background-image: url(../images/common/svg/decor/main/arrow_form.svg)
    background-repeat: no-repeat
    background-position: center
    background-size: contain
    position: absolute
    top: 45px
    left: 225px
    z-index: 1
    @media (max-width: 360px)
      left: 200px
    @media (min-width: 768px)
      left: calc(50% + 30px)
    @media (min-width: 1024px)
      left: calc(33.3% + 100px)
    @media (min-width: 1200px)
      left: calc(50% + 52px)
      width: 142px
      height: 142px
      top: 70px
  .container
    position: relative
    &:before
      content: ''
      display: block
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      background-color: $accent-color
      @media (min-width: 960px)
        width: calc(100% - 30px)
        left: 15px
        border-radius: 30px
      @media (min-width: 1200px)
        width: calc(100% + 160px)
        left: -80px
  &__title
    font-weight: 700
    font-size: 22px
    line-height: 25px
    color: #fff
    text-align: center
    @media (min-width: 576px)
      font-size: 24px
      line-height: 28px
    @media (min-width: 768px)
      text-align: left
    @media (min-width: 1200px)
      font-size: 31px
      line-height: 36px
  &__list
    color: #fff
    margin-top: 20px
    @media (min-width: 1024px)
      margin-top: 35px
    @media (min-width: 1200px)
      margin-top: 45px
    ul
      margin: 0
      padding: 0
      list-style-type: none
      letter-spacing: .3px
      li
        display: block
        width: 100%
        position: relative
        padding-left: 35px
        margin-top: 22px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e805'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          font-size: 18px
          line-height: 18px
          color: #fff
          position: absolute
          top: 1px
          left: 1px
  &__form
    margin-top: 35px
    @media (min-width: 768px)
      margin-top: 0
    .input-group
      margin-top: 28px
      @media (min-width: 1200px)
        margin-top: 25px
      &:nth-of-type(1)
        margin-top: 0
      &.check-group
        margin-top: 12px
  &__subtitle
    color: #fff
    font-weight: 700
    text-align: center
    font-size: 20px
    line-height: 30px
    letter-spacing: .5px
    max-width: 270px
    margin: 0 auto
    position: relative
    @media (min-width: 768px)
      max-width: 310px
    @media (min-width: 960px)
      max-width: 360px
      text-align: left
      margin: 0 auto 0 0
      padding: 0
    @media (min-width: 1024px)
      max-width: 100%
      text-align: center
    @media (min-width: 1200px)
      padding-bottom: 45px
    &:before
      content: url("../images/common/svg/decor/main/decor_star1.svg")
      display: block
      position: absolute
      bottom: calc(100% + 5px)
      left: 32px
      @media (min-width: 768px)
        bottom: calc(100% - 30px)
        left: -25px
      @media (min-width: 960px)
        left: -55px
      @media (min-width: 1024px)
        left: 0
        top: -70px
      @media (min-width: 1200px)
        left: 28px
        top: -62px
    &:after
      content: url("../images/common/svg/decor/main/decor_star3.svg")
      display: block
      position: absolute
      top: calc(100% + 25px)
      right: 15px
      @media (min-width: 768px)
        top: calc(100% - 20px)
        right: -50px
      @media (min-width: 960px)
        right: 0
      @media (min-width: 1024px)
        top: calc(100% + 20px)
      @media (min-width: 1200px)
        top: calc(100% - 20px)
    span
      text-transform: uppercase
  &__content
    position: relative
    z-index: 1
    padding: 55px 0
    @media (min-width: 576px)
      max-width: 450px
      margin: 0 auto
    @media (min-width: 768px)
      display: flex
      align-items: flex-start
      justify-content: flex-start
      flex-wrap: wrap
      max-width: 100%
    @media (min-width: 960px)
      max-width: 810px
    @media (min-width: 1024px)
      max-width: 100%
      align-items: stretch
      padding: 40px
    @media (min-width: 1200px)
      padding: 62px 0
    &__column
      &:nth-of-type(1)
        position: relative
        z-index: 1
        @media (min-width: 768px)
          flex: 0 0 100%
          padding-right: 335px
        @media (min-width: 1024px)
          flex: 0 0 33.3%
          padding: 0
        @media (min-width: 1200px)
          flex: 0 0 40%
          max-width: 40%
      &:nth-of-type(2)
        position: relative
        z-index: 1
        @media (min-width: 768px)
          flex: 0 0 315px
          max-width: 315px
          position: absolute
          top: 55px
          right: 0
        @media (min-width: 1024px)
          flex: 0 0 33%
          max-width: 33%
          margin: 0 auto
          position: relative
          top: 0
        @media (min-width: 1200px)
          flex: 0 0 30%
          max-width: 30%
          margin: 0 auto
      &:nth-of-type(3)
        margin-top: 50px
        display: flex
        align-items: center
        justify-content: center
        flex-wrap: wrap
        min-height: 210px
        position: relative
        @media (min-width: 768px)
          flex: 0 0 100%
          padding-right: 365px
          margin-top: 20px
          min-height: 125px
        @media (min-width: 1024px)
          flex: 0 0 30%
          max-width: 30%
          padding-right: 0
          margin-top: 0
          min-height: auto
        @media (min-width: 1200px)
          flex: 0 0 23%
          max-width: 23%
        &:after
          content: url("../images/common/svg/decor/main/form_bg.svg")
          position: absolute
          width: auto
          height: auto
          top: calc(50% + 30px)
          left: 50%
          margin-right: -50%
          transform: translate(-50%, -50%)
          @media (min-width: 768px)
            top: -50%
            opacity: .5
          @media (min-width: 1024px)
            top: 50%
            opacity: 1

.platform-block
  padding-top: 55px
  @media (min-width: 1200px)
    padding-top: 85px
  &__title
    @media (min-width: 1200px)
      text-align: left
  &__content
    background: $main-gradient
    margin-top: 32px
    border-radius: 25px
    padding: 25px 30px
    @media (min-width: 768px)
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
      padding: 40px 30px
    @media (min-width: 1200px)
      margin-top: 40px
      padding: 40px 42px
      align-items: flex-end
    &__left-side
      @media (min-width: 768px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin-right: 20px
      .side-title
        text-transform: uppercase
        text-align: center
        font-size: 20px
        line-height: 25px
        letter-spacing: .65px
        @media (min-width: 1200px)
          font-size: 22px
          text-align: left
          padding-left: 50px
          letter-spacing: 2.1px
      ul
        margin: 30px 0 0
        padding: 0
        list-style-type: none
        @media (min-width: 1200px)
          margin-top: 45px
          max-width: 455px
        li
          display: block
          width: 100%
          position: relative
          padding-left: 35px
          margin-top: 20px
          @media (min-width: 1200px)
            padding-left: 52px
            margin-top: 25px
          &:nth-of-type(1)
            margin-top: 0
          &:before
            content: '\e805'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            color: #fff
            font-size: 18px
            line-height: 18px
            position: absolute
            top: 1px
            left: 1px
      .buy-btn
        margin-top: 38px
        @media (min-width: 576px)
          max-width: 265px
          margin: 38px auto 0
        @media (min-width: 768px)
          margin: 35px 0 0 35px
        @media (min-width: 1200px)
          margin: 35px 0 0 55px
    &__right-side
      margin-top: 38px
      @media (min-width: 768px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
      @media (min-width: 1200px)
        margin-bottom: 15px
      .side-subtitle
        text-align: center
        font-weight: 700
        @media (min-width: 576px)
          font-size: 18px
        @media (min-width: 1200px)
          text-align: center
          font-size: 20px
          max-width: 450px
      ul
        display: flex
        align-items: flex-start
        justify-content: center
        flex-wrap: wrap
        margin: 20px 0 0 0
        padding: 0
        list-style-type: none
        li
          flex: 0 0 auto
          width: auto
          color: #fff
          border: 2px solid #fff
          padding: 11px 10px
          border-radius: 12px
          font-size: 10px
          line-height: 12px
          text-transform: uppercase
          letter-spacing: .3px
          margin: 0 5px 12px
          @media (min-width: 576px)
            font-size: 12px
            margin: 0 10px 12px
          @media (min-width: 768px)
            font-size: 14px
            margin: 0 10px 15px
          @media (min-width: 1200px)
            font-size: 16px
            padding: 23px 25px
            border-radius: 20px
            letter-spacing: 0

.training-types
  padding-top: 40px
  @media (min-width: 1200px)
    padding-top: 80px
  &__title
    font-weight: 700
    text-align: center
    font-size: 18px
    max-width: 285px
    margin: 0 auto
    @media (min-width: 576px)
      max-width: 100%
      font-size: 20px
    @media (min-width: 1200px)
      text-align: left
  &__content
    margin-top: 20px
    @media (min-width: 768px)
      margin-top: 40px
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
    &__item
      border-radius: 25px
      padding: 32px
      margin-top: 20px
      @media (min-width: 768px)
        display: flex
        flex-direction: column
        align-items: center
        justify-content: flex-start
        flex: 0 0 calc(33.3% - 10px)
        max-width: calc(33.3% - 10px)
        margin: 0 15px 0 0
        padding: 20px 15px
        &:nth-of-type(3n)
          margin-right: 0
      @media (min-width: 960px)
        padding: 20px 30px
      @media (min-width: 1200px)
        flex: 0 0 calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        margin: 0 30px 0 0
        text-align: left
        align-items: flex-start
        padding: 40px 35px
      &:nth-of-type(1)
        margin-top: 0
      .item-title
        text-align: center
        text-transform: uppercase
        font-size: 20px
        line-height: 20px
        letter-spacing: .65px
        @media (min-width: 1200px)
          font-size: 24px
          line-height: 30px
      .item-descr
        margin: 20px 0
        @media (min-width: 576px)
          text-align: center
          max-width: 400px
          margin: 20px auto
        @media (min-width: 768px)
          font-size: 14px
        @media (min-width: 960px)
          text-align: left
        @media (min-width: 1200px)
          font-size: 16px
          line-height: 25px
          margin: 10px 0 20px
      .item-btn
        @media (min-width: 576px)
          max-width: 255px
          margin: 0 auto
        @media (min-width: 768px)
          margin-top: auto
        @media (min-width: 1200px)
          margin: auto 0 0
          max-width: 185px
.curses-block
  padding-top: 60px
  position: relative
  @media (min-width: 1200px)
    padding-top: 75px
  &:before
    content: ''
    display: block
    width: 100%
    height: calc(100% - 300px)
    background-color: $gray-darkness-color
    position: absolute
    top: 300px
    left: 0
  &__title
    @media (min-width: 768px)
      text-align: left
      max-width: 500px
    @media (min-width: 960px)
      max-width: 100%
  &__content
    background-color: $gray-color
    border-radius: 20px
    padding: 40px 30px
    position: relative
    z-index: 1
    @media (min-width: 1200px)
      border-radius: 40px
      padding: 80px 30px
  &__choose
    margin-top: 20px
    @media (min-width: 768px)
      max-width: 280px
    @media (min-width: 1200px)
      margin-top: 38px
      max-width: 300px
    .choose-title
      @media (min-width: 1200px)
        font-weight: 700
    .choices
      margin-top: 15px
      @media (min-width: 1200px)
        max-width: 260px
      &__inner
        border: 1px solid #DBDEE0
        border-radius: 4px
        background-color: transparent
        padding: 7px !important
        min-height: auto
        @media (min-width: 768px)
          background-color: #fff
        @media (min-width: 1200px)
          padding: 14px 5px!important
        .choices__list
          .choices__item
            font-weight: 700
            font-size: 12px
            line-height: 12px
            color: $txt-color
            letter-spacing: 0
  &__tabs
    margin-top: 40px
    &__item
      display: none
      flex-direction: column-reverse
      align-items: flex-start
      justify-content: flex-start
      @media (min-width: 768px)
        flex-direction: row
        flex-wrap: wrap
      .tab__left-side
        width: 100%
        margin-top: 28px
        @media (min-width: 768px)
          flex: 0 0 60%
          max-width: 60%
          margin-top: 0
          padding-right: 20px
        @media (min-width: 1200px)
          flex: 0 0 50%
          max-width: 50%
          padding-right: 40px
        .tab-title
          text-align: center
          font-weight: 700
          @media (min-width: 768px)
            font-size: 18px
            text-align: left
          @media (min-width: 1200px)
            font-size: 20px
            line-height: 24px
            letter-spacing: .55px
        .tab-descr
          margin-top: 20px
          @media (min-width: 1200px)
            margin-top: 35px
            padding-left: 15px
            letter-spacing: .3px
          ul
            margin: 0
            padding: 0
            list-style-type: none
            li
              display: block
              width: 100%
              position: relative
              padding-left: 35px
              margin-top: 20px
              @media (min-width: 1200px)
                padding-left: 50px
                margin-top: 28px
              &:nth-of-type(1)
                margin-top: 0
              &:before
                content: '\e805'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: block
                margin: 0
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                color: $accent-color
                font-size: 18px
                line-height: 18px
                position: absolute
                top: 1px
                left: 1px
      .tab__right-side
        width: 100%
        @media (min-width: 768px)
          flex: 0 0 40%
          max-width: 40%
        @media (min-width: 1200px)
          flex: 0 0 50%
          max-width: 50%
          padding-left: 15px
        .tab-image
          display: block
          width: 100%
          max-width: 100%
          border-radius: 25px
          @media (min-width: 1200px)
            width: auto
      &.active
        display: flex
  &__btn-row
    margin-top: 42px
    display: flex
    align-items: center
    justify-content: center
    @media (min-width: 1200px)
      margin-top: 60px
    .all-curses
      display: inline-block
      text-decoration: underline
      font-weight: 700
      font-size: 14px
      line-height: 20px
      letter-spacing: .3px
      color: $txt-color
      transition: color .3s
      &:hover
        color: $accent-color
.cards-block
  background-color: $gray-darkness-color
  padding-top: 55px
  @media (min-width: 1200px)
    padding-top: 75px
  .title-row
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-start
    @media (min-width: 1024px)
      flex-direction: row
    @media (min-width: 1200px)
      align-items: flex-end
  &__title
    @media (min-width: 1024px)
      flex: 0 0 calc(40% - 20px)
      max-width: calc(40% - 20px)
      text-align: left
    @media (min-width: 1200px)
      flex: 0 0 auto
      width: auto
      max-width: 365px
  &__subtitle
    font-family: Veles, sans-serif
    color: $accent-color
    font-weight: 700
    font-size: 20px
    line-height: 27px
    letter-spacing: 0
    text-align: center
    max-width: 270px
    margin: 25px auto
    @media (min-width: 576px)
      font-size: 24px
      line-height: 30px
      max-width: 320px
    @media (min-width: 768px)
      font-size: 26px
      line-height: 35px
      max-width: 350px
    @media (min-width: 1024px)
      flex: 0 0 calc(60% - 20px)
      max-width: calc(60% - 20px)
      text-align: left
      margin-left: 40px
    @media (min-width: 1200px)
      flex: 0 0 auto
      max-width: 100%
      width: auto
      font-size: 30px
      line-height: 40px
      margin: 0 0 0 auto
  &__content
    @media (min-width: 1024px)
      margin-top: 40px
    @media (min-width: 1200px)
      // padding-left: calc(((100vw - 1140px) / 2) - 15px)
    .cards-slider
      @media (min-width: 1024px)
        margin-left: -30px
      @media (min-width: 1200px)
        margin-left: -155px
      .slider-item
        margin: 0 15px 0 15px
        max-width: calc(100% - 30px)
        border-radius: 25px
        overflow: hidden
        background-color: #fff
        @media (min-width: 576px)
          max-width: 420px
        @media (min-width: 768px)
          max-width: 400px
        @media (min-width: 1024px)
          max-width: 920px
          margin: 0 30px 0 0
        &__content
          display: flex
          flex-direction: column-reverse
          align-items: flex-start
          justify-content: flex-start
          @media (min-width: 1024px)
            flex-direction: row
            align-items: stretch
          &__left-side
            width: 100%
            padding: 20px 32px
            @media (min-width: 768px)
              padding: 25px 20px
            @media (min-width: 1024px)
              flex: 0 0 calc(100% - 350px)
              max-width: calc(100% - 350px)
              padding: 40px 65px
            .cards-title
              display: block
              text-transform: uppercase
              font-size: 20px
              line-height: 25px
              color: #000
              letter-spacing: .65px
              @media (min-width: 1024px)
                font-size: 24px
                line-height: 30px
            .cards-level
              display: inline-block
              font-weight: 700
              font-size: 12px
              line-height: 20px
              margin-top: 5px
              @media (min-width: 768px)
                margin-top: 10px
              @media (min-width: 1024px)
                margin-top: 22px
                letter-spacing: .3px
            .cards-descr
              margin-top: 22px
              @media (min-width: 1024px)
                margin-top: 35px
                line-height: 24px
              ul
                margin: 0
                padding: 0
                list-style-type: none
                li
                  display: block
                  padding-left: 30px
                  position: relative
                  margin-top: 20px
                  @media (min-width: 1024px)
                    margin-top: 25px
                  &:before
                    content: ''
                    display: block
                    width: 6px
                    height: 6px
                    background-color: $txt-color
                    border-radius: 50%
                    position: absolute
                    top: 6px
                    left: 0
                  &:nth-of-type(1)
                    margin-top: 0
            .more-link
              margin: 38px 0 20px 0
              @media (min-width: 768px)
                margin: 35px 0 20px 35px
                max-width: 180px
              @media (min-width: 1200px)
                margin: 35px 0 0 0
                max-width: 130px
          &__right-side
            width: 100%
            position: relative
            min-height: 175px
            overflow: hidden
            background-repeat: no-repeat
            background-position: center
            background-size: cover
            @media (min-width: 1024px)
              flex: 0 0 350px
              max-width: 350px
              max-height: 100%
            .tags-list
              position: absolute
              display: flex
              align-items: flex-start
              justify-content: flex-start
              flex-wrap: wrap
              top: 20px
              right: 0
              @media (min-width: 1024px)
                top: 40px
              &__item
                background-color: $green-color
                color: #fff
                border-radius: 17px
                text-align: center
                font-weight: 700
                font-size: 12px
                line-height: 20px
                letter-spacing: .13px
                text-transform: uppercase
                margin-left: 10px
                padding: 5px 15px
                @media (min-width: 1024px)
                  padding: 6px 16px
                &:last-child
                  border-radius: 17px 0 0 17px
            .slide-image
              display: block
              width: 100%
              height: auto
              margin: 0
  .navigation-row
    display: flex
    align-items: center
    justify-content: center
    position: relative
    margin: 45px 0 65px
    @media (min-width: 768px)
      padding: 0 100px
    .slider-btns
      display: flex
      align-items: center
      justify-content: space-between
      position: absolute
      top: 50%
      left: 0
      width: 100%
      height: 0
      @media (min-width: 768px)
        width: auto
      .slider-btn
        font-size: 28px
        line-height: 28px
        color: #000
        transition: opacity .3s
        cursor: pointer
        &:hover
          color: $accent-color
        &.swiper-button-disabled
          opacity: .5
          &:hover
            color: #000
