/* import animates here */
@keyframes scaleIcon
  0%
    transform: scale(1)
  50%
    transform: scale(1.1)
  100%
    transform: scale(1)
@keyframes my_zoom
  0%
    transform: scale(1)
    opacity: 0
  50%
    transform: scale(1)
    opacity: 1
  100%
    transform: scale(1.5)
    opacity: 0