.iti
  width: 100%
.iti__flag
  background-image: url("../images/common/flags/flags.png")
  transform: scale(1.4)
  border: 0.5px solid #eee
  border-radius: 3px
  overflow: hidden
  box-shadow: none
  @media (max-width: 960px)
    transform: none
.iti--separate-dial-code .iti__selected-flag
  background-color: transparent
.iti__selected-flag
  padding: 0 6px 0 20px
  @media (max-width: 1024px)
    padding: 0 6px 0 15px
  @media (max-width: 960px)
    padding: 0 0 0 10px
.iti--separate-dial-code .iti__selected-dial-code
  color: $txt-color
  font-family: $default-font
  font-size: 20px
  line-height: 20px
  margin-left: 10px
  @media (max-width: 1024px)
    font-size: 16px
    margin-left: 8px
  @media (max-width: 960px)
    font-size: 14px
    margin-left: 5px
.iti__arrow
  margin-left: 8px
  width: 0
  height: 0
  border-left: 6px solid transparent
  border-right: 6px solid transparent
  border-top: 6px solid $txt-color
  @media (max-width: 960px)
    margin-left: 5px
    border-left: 4px solid transparent
    border-right: 4px solid transparent
    border-top: 4px solid $txt-color
.iti__arrow--up
  border-top: none
  border-bottom: 6px solid $txt-color

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)
  .iti__flag
    background-image: url("../images/common/flags/flags@2x.png")