.section-navigation
  display: none
  position: fixed
  top: 0
  right: 20px
  height: var(--app-height)
  width: auto
  padding: var(--header-height) 0
  transform: translateX(100px)
  transition: transform .5s
  z-index: 1
  @media (min-width: 1200px)
    display: block
  &.show
    transform: translateX(0)
  .navigation-list
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    height: 100%
    &__item
      flex: 0 0 10px
      width: 10px
      height: 10px
      border: 2px solid #B3B1BA
      border-radius: 50%
      background-color: transparent
      margin: 9px 0
      cursor: pointer
      transition: background-color .3s, border-color .3s
      &.active
        background-color: $accent-color
        border-color: $accent-color
.footer
  background-color: $txt-color
  padding: 42px 0
  @media (min-width: 1200px)
    padding: 45px 0
  &__top
    &__content
      @media (min-width: 768px)
        display: flex
        align-items: flex-start
        justify-content: flex-start
        flex-wrap: wrap
    &__column
      margin: 0 0 22px 0
      @media (min-width: 768px)
        flex: 0 0 calc(50% - 40px)
        max-width: calc(50% - 40px)
        margin: 0 80px 22px 0
        &:nth-of-type(2n)
          margin-right: 0
        &:nth-of-type(3)
          flex: 0 0 100%
          max-width: 100%
          margin: 0
          display: flex
          flex-direction: row
          align-items: flex-start
          justify-content: flex-start
          flex-wrap: wrap
          .menu-item
            flex: 0 0 calc(50% - 40px)
            max-width: calc(50% - 40px)
            margin: 0 80px 22px 0
          .main-item
            flex: 0 0 100%
            max-width: 100%
            margin-right: 0
            .main-list
              display: flex
              flex-direction: row
              align-items: flex-start
              justify-content: space-between
              flex-wrap: wrap
              border-top: 1px solid rgba(255,255,255,.2)
              &__item
                flex: 0 0 auto
                width: auto
                margin: 22px 0 0
                &:nth-of-type(1)
                  margin-top: 22px
      @media (min-width: 1200px)
        flex: 0 0 calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        margin: 0 30px 25px 0
        &:nth-of-type(2n)
          margin-right: 30px
        &:nth-of-type(3n)
          margin-right: 0
          flex: 0 0 calc(33.3% - 20px)
          max-width: calc(33.3% - 20px)
          .menu-item
            flex: 0 0 100%
            max-width: 100%
            margin: 25px 0 0
          .main-item
            margin-top: 0
            .main-list
              border: none
              &__item
                flex: 0 0 100%
                max-width: 100%
                margin: 15px 0 0
      .menu-item,
      .main-item
        margin-top: 22px
        position: relative
        @media (min-width: 1200px)
          margin-top: 25px
        &:nth-of-type(1)
          margin-top: 0
        .menu-title
          display: block
          width: 100%
          max-width: calc(100% - 40px)
          position: relative
          font-weight: 700
          font-size: 14px
          line-height: 20px
          color: $accent-color
          letter-spacing: .3px
          text-transform: uppercase
          text-decoration: none
        .submenu-link
          display: flex
          align-items: center
          justify-content: center
          width: 24px
          height: 20px
          position: absolute
          top: 0
          right: 0
          color: $accent-color
          font-size: 14px
          line-height: 20px
          @media (min-width: 1200px)
            display: none
        .menu-list
          display: none
          margin: 10px 0 0
          padding: 0
          list-style-type: none
          @media (min-width: 1200px)
            display: block
          &__item
            display: block
            margin-top: 15px
            @media (min-width: 1200px)
              margin-top: 10px
            &:nth-of-type(1)
              margin-top: 0
          &__link
            color: #fff
            text-decoration: none
            font-size: 14px
            line-height: 20px
            letter-spacing: .35px
            &:hover
              text-decoration: underline
        .main-list
          &__item
            margin-top: 22px
            &:nth-of-type(1)
              margin-top: 0
            a
              font-weight: 700
              font-size: 14px
              line-height: 20px
              color: $accent-color
              letter-spacing: .3px
              text-transform: uppercase
              position: relative
              transition: color .3s
              &:before
                content: ''
                display: block
                width: 0
                height: 1px
                background-color: $accent-color
                position: absolute
                bottom: 0
                left: 0
                transition: width .3s, background-color .3s
              &:hover
                color: #fff
                text-decoration: none
                &:before
                  width: 100%
                  background-color: #fff
        &.show
          .submenu-link
            transform: scale(1,-1)
  &__bottom
    margin-top: 25px
    padding: 25px 0 0
    border-top: 1px solid rgba(255, 255, 255, 0.2)
    @media (min-width: 1200px)
      padding: 32px 0 0
    &__content
      @media (min-width: 768px)
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
    &__column
      margin: 0 0 25px 0
      @media (min-width: 768px)
        flex: 0 0 calc(33.3% - 10px)
        max-width: calc(33.3% - 10px)
        margin: 0 15px 0 0
        &:nth-of-type(3n)
          margin-right: 0
      @media (min-width: 1200px)
        flex: 0 0 calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        margin: 0 30px 0 0
      &:last-child
        margin-bottom: 0
      .social-links
        display: flex
        align-items: center
        justify-content: flex-start
        margin: 0
        padding: 15px 0
        list-style-type: none
        @media (min-width: 576px)
          justify-content: center
        &__item
          flex: 0 0 auto
          width: auto
          margin: 0 auto
          @media (min-width: 576px)
            margin: 0 25px
          @media (min-width: 768px)
            margin: 0 15px
          @media (min-width: 1200px)
            margin: 0 52px 0 0
            &:last-child
              margin-right: 0
        &__link
          color: #fff
          font-size: 21px
          line-height: 25px
          text-decoration: none
          transition: color .3s
          .icon.icon-youtube
            font-size: 26px
            line-height: 20px
          .icon.icon-vk
            font-size: 26px
            line-height: 20px
          &:hover
            color: $accent-color
      .backcall-btn
        color: #fff
        font-size: 16px
        line-height: 16px
        @media (min-width: 576px)
          max-width: 255px
          margin: 0 auto
      .apps-links
        display: flex
        align-items: center
        justify-content: space-between
        flex-wrap: wrap
        padding: 15px 0
        @media (min-width: 576px)
          justify-content: center
        &__item
          flex: 0 0 auto
          width: auto
          margin: 0
          @media (min-width: 576px)
            margin: 0 20px
          @media (min-width: 768px)
            margin: 0 auto
          @media (min-width: 1200px)
            margin: 0 20px 0 0
        &__link
          display: block
          transition: transform .3s
          &:hover
            transform: translateY(-5px)
          .app-image
            display: block
            width: 160px
            @media (max-width: 360px)
              width: 140px
            @media (min-width: 768px)
              width: 110px
  &__copy
    margin-top: 25px
    @media (max-width: 1200px)
      margin-top: 20px
    &__content
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      @media (min-width: 1200px)
        flex-direction: row
        flex-wrap: wrap
    &__column
      width: 100%
      &:nth-of-type(1)
        display: flex
        align-items: center
        justify-content: flex-start
        order: 3
        margin-top: 20px
        @media (min-width: 576px)
          justify-content: center
        @media (min-width: 1200px)
          flex: 0 0 50%
          max-width: 50%
          margin-top: 30px
          justify-content: flex-end
        .copyright
          display: inline-block
          color: #fff
          font-size: 12px
          line-height: 20px
          letter-spacing: 0
          @media (min-width: 1200px)
            line-height: 21px
      &:nth-of-type(2)
        order: -1
        @media (min-width: 1200px)
          flex: 0 0 100%
          max-width: 100%
        .payment-list
          display: flex
          align-items: center
          justify-content: space-around
          flex-wrap: wrap
          @media (min-width: 1024px)
            justify-content: center
          &__item
            flex: 0 0 auto
            width: auto
            @media (min-width: 1024px)
              margin: 0 20px
            .payment-image
              min-height: 22px
      &:nth-of-type(3)
        margin-top: 30px
        @media (min-width: 1200px)
          flex: 0 0 50%
          max-width: 50%
        .info-links
          @media (min-width: 576px)
            display: flex
            align-items: flex-start
            justify-content: center
          @media (min-width: 1200px)
            justify-content: flex-start
          &__item
            width: 100%
            margin: 15px 0 0 0
            @media (min-width: 576px)
              flex: 0 0 auto
              width: auto
              margin: 0 15px
            @media (min-width: 1200px)
              margin: 0 30px 0 0
              display: inline-block
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: inline-block
            color: #fff
            text-decoration: underline
            font-size: 12px
            line-height: 12px
            &:hover
              text-decoration: none
.totop_btn
  display: none
  width: 64px
  height: 64px
  position: fixed
  bottom: 40px
  right: 40px
  font-size: 64px
  line-height: 64px
  color: $accent-color
  z-index: 10